Area{\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    width: 1000px;\n    height: 54px;\n    margin-left: 20px;\n    background-color: var(--primary-color);\n    border-radius: 15px;\n    border: 2px solid var(--secondary-color);\n}\n\n.searchIcon{\n    height: 40px;\n    padding: 2px 5px;\n    border-right: 2px solid black;\n    filter: var(--secondary-color-svg-filter);\n    cursor: pointer;\n}\n\n.searchIcon:hover{\n    filter: var(--text-color-svg-filter);\n}\n\n#searchInput{\n    width: 100%;\n    height: 100%;\n    border: none;\n    background-color: transparent;\n    color: var(--text-color);\n    font-size: 20px;\n    padding: 0px 10px;\n}\n\n#searchInput:focus{\n    outline: none;\n}\n\n.filterIcon{\n    height: 35px;\n    padding: 0px 10px;\n    rotate: -90deg;\n    filter: var(--secondary-color-svg-filter);\n    cursor: pointer;\n}\n\n.filterIcon:hover{\n    filter: var(--text-color-svg-filter);\n}\n\n.headerAccountArea{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\n#themeArea{\n    display: flex;\n    margin: 0 auto;\n    height: 100%;\n    justify-content: center;\n    align-items: center;\n}\n\n#themeSwitch{\n    display: flex;\n    align-items: center;\n    background-color: var(--button-color);\n    height: 25px;\n    width: 60px;\n    border-radius: 5px;\n    cursor: pointer;\n}\n\n#themeButton{\n    border: 2px solid var(--secondary-color);\n    background-color: var(--primary-color);\n    width: 30px;\n    padding: 0;\n    margin-top: 1px;\n    height: 30px;\n    border-radius: 5px;\n    transition: linear 0.2s;\n}\n\n#themeButton img{\n    width: 100%;\n    height: 100%;\n    filter: var(--text-color-svg-filter);\n    transition: linear 0.3s;\n}\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\n#openLoginButton{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background-color: transparent;\n    padding: 0px 20px;\n    border: 2px solid var(--button-color);\n    height: 60px;\n    border-radius: 20px;\n    color: var(--button-color);\n    font-size: 22px;\n    cursor: pointer;\n    transition: 300ms;\n}\n\n#openLoginButton:hover{\n    background-color: var(--button-color);\n    color: var(--bg-color);\n    filter: drop-shadow(0px 0px 7px var(--button-color));\n}\n\n#loginArea {\n    display: none;\n    position: fixed;\n    z-index: 1;\n    width: 100%;\n    height: 100%;\n    overflow: auto;\n    background-color: rgba(0, 0, 0, 0.5);\n    padding-top: calc(50vh - 300px);\n}\n\n.loginForm {\n    background-color: var(--primary-color);\n    margin: 0 auto;\n    border-radius: 30px;\n    border: 2px solid var(--secondary-color);\n    width: 400px;\n    height: 600px;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n    align-items: center;\n    box-shadow: 0px 0px 10px var(--secondary-color);\n}\n\n.avatarContainer {\n    border-radius: 50%;\n    border: 2px solid var(--secondary-color);\n    object-fit: cover;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 200px;\n    height: 200px;\n    overflow: hidden;\n    background-color: var(--secondary-color);\n    box-shadow: 0px 0px 10px var(--secondary-color);\n}\n\n.avatarContainer img {\n    width: 100%;\n    filter: var(--primary-color-svg-filter);\n}\n\n.loginFormContainer {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n    align-items: center;\n    width: 100%;\n}\n\n#usernameInput,\n#passwordInput {\n    width: 70%;\n    height: 30px;\n    border-radius: 15px;\n    border: 2px solid var(--secondary-color);\n    background-color: var(--secondary-color);\n    color: var(--text-color);\n    font-size: 16px;\n    padding-left: 10px;\n    text-align: center;\n    margin-bottom: 10px;\n}\n\n#usernameInput:focus,\n#passwordInput:focus {\n    outline: none;\n}\n\n#usernameInput::placeholder,\n#passwordInput::placeholder {\n    color: var(--text-color);\n}\n\n#submitLoginButton{\n    background-color: transparent;\n    height: 40px;\n    padding: 0px 10px;\n    border: 2px solid var(--button-color);\n    border-radius: 15px;\n    color: var(--button-color);\n    font-size: 18px;\n    cursor: pointer;\n    transition: 300ms;\n    margin-bottom: 10px;\n}\n\n#submitLoginButton:hover{\n    background-color: var(--button-color);\n    color: var(--bg-color);\n    filter: drop-shadow(0px 0px 7px var(--button-color));\n}\n\n#registerLink{\n    color: var(--text-color);\n    font-size: 16px;\n    margin-top: 15px;\n    height: 20px;\n    transition: 100ms all ease-in-out;\n}\n\n#registerLink:hover{\n    font-size: 18px;\n}\n\n#rememberText{\n    color: var(--text-color);\n    font-size: 14px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 5px;\n}\n\n.loginOthersContainer{\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n    align-items: center;\n}\n\n#forgotPassword{\n    color: var(--text-color-2);\n}\n\n#forgotPassword:hover{\n    text-decoration: underline;\n}\n\n#closeLoginButton {\n    color: var(--text-color);\n    font-size: 16px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 5px 10px;\n    border: 2px solid var(--text-color);\n    border-radius: 10px;\n    height: 30px;\n    margin-top: 10px;\n    background-color: transparent;\n    transition: 200ms all ease-in-out;\n}\n\n#closeLoginButton:hover{\n    color: var(--primary-color);\n    background-color: var(--text-color);\n    cursor: pointer;\n}\n\n.animate {\n    -webkit-animation: animatezoom 0.6s;\n    animation: animatezoom 0.6s\n}\n\n@-webkit-keyframes animatezoom {\n    from {\n        -webkit-transform: scale(0)\n    }\n\n    to {\n        -webkit-transform: scale(1)\n    }\n}\n\n@keyframes animatezoom {\n    from {\n        transform: scale(0)\n    }\n\n    to {\n        transform: scale(1)\n    }\n}\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\nmain{\n    display: flex;\n    flex-wrap: nowrap;\n    width: 100%;\n    height: 100%;\n}\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\n.sideMenuArea{\n    display: flex;\n    width: 300px;\n    margin-top: 20px;\n    padding: 30px 0;\n    border-right: 1px solid var(--secondary-color);\n}\n\n.sideMenuArea ul li{\n    position: relative;\n    display: flex;\n    align-items: center;\n    list-style: none;\n    width: 100%;\n    padding: 10px 0px 10px 15px;\n    border-radius: 0 10px 10px 0;\n}\n\n.sideMenuArea ul li:hover{\n    background-color: var(--primary-color);\n}\n\n.sideMenuArea ul li:hover::after{\n    background-color: var(--secondary-color);\n    content: \"\";\n    height: 100%;\n    position: absolute;\n    left: 0;\n    top: 0;\n    width: 5px;\n}\n\nbody main .sideMenuArea ul .selectedLink{\n    background-color: var(--secondary-color);\n}\n\nbody main .sideMenuArea ul .selectedLink::after{\n    background-color: var(--button-color);\n    content: \"\";\n    height: 100%;\n    position: absolute;\n    left: 0;\n    top: 0;\n    width: 5px;\n}\n\n.sideMenuIcon{\n    height: 30px;\n    filter: var(--text-color-svg-filter);\n    cursor: pointer;\n}\n\n#remindersLink .sideMenuIcon{\n    rotate: 45deg;\n}\n\n.sideMenuText{\n    font-size: 20px;\n    color: var(--text-color);\n    padding-left: 10px;\n    cursor: pointer;\n}\n\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\n.workArea{\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    margin-top: 20px;\n    padding: 30px;\n}\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\n.welcomeScreen{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n    padding-top: 100px;\n}\n\n.welcomeScreen h1{\n    color: var(--text-color);\n    font-size: 30px;\n    margin-bottom: 15px;\n}\n\n.welcomeScreen p{\n    color: var(--secondary-color);\n    font-size: 20px;\n    margin-top: 15px;\n}\n\n.welcomeScreen div{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.welcomeScreenIcon{\n    height: 40px;\n    filter: var(--text-color-svg-filter);\n    margin: 0 5px;\n}\n\n.welcomeScreenLogo{\n    height: 120px;\n    margin: 0 30px;\n}\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\n.scheduleScreen{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n}\n\n#scheduleTable{\n    color: var(--text-color);\n    width: 1000px;\n    vertical-align: center;\n    text-align: center;;\n    border-collapse: collapse;\n    box-shadow: 0px 0px 20px 5px var(--shadow-color);\n    border-radius: 20px;\n}\n\n.scheduleScreen th{\n    border-right: 2px solid var(--secondary-color);\n    border-left: 2px solid var(--secondary-color);\n}\n\n.scheduleScreen th:nth-child(1){\n    border-left: none;\n}\n\n.scheduleScreen th:nth-child(6){\n    border-right: none;\n}\n\n.scheduleScreen td{\n    background-color: var(--secondary-color);\n    border: 2px solid var(--primary-color);\n}\n\n.scheduleScreen tr:nth-child(even) td{\n    background-color: var(--tertiary-color);\n}\n\n.scheduleScreen tr, td{\n    width: 100px;\n    height: 75px;\n    max-width: 100px;\n    font-size: 18px;\n    overflow: hidden;\n}\n\n.scheduleScreen tr:nth-child(even){\n    background-color: var(--secondary-color);\n    border: 2px solid var(red);\n    border-left: 2px solid var(--primary-color);;\n}\n\n.scheduleScreen table tr:nth-child(1){\n    height: 40px;\n    font-size: 20px;\n    font-weight: 900;\n}\n\n.scheduleScreen table p{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-wrap: wrap;\n    height: 100%;\n    width: 100%;\n    font-size: 16px;\n}\n\n.scheduleScreen table td input{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    text-align: center;\n    flex-wrap: wrap;\n    height: 100%;\n    width: 100%;\n    background-color: transparent;\n    color: var(--text-color);\n    font-size: 16px;\n}\n\n.scheduleScreen table td input:focus{\n    outline: solid 2px var(--text-color);\n}\n\n#lockTableButton{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    border: 2px solid var(--button-color);\n    border: none;\n    width: 1000px;\n    border-radius: 0 0 20px 20px;\n    background-color: var(--bg-color);\n    padding: 5px;\n    box-shadow: 0px 0px 20px 5px var(--shadow-color);\n}\n\n#lockTableButton img{\n    height: 50px;\n    filter: var(--button-color-svg-filter);\n    transition: 300ms all ease-in-out;\n}\n\n#lockTableButton:hover{\n    background-color: var(--button-color);\n    color: var(--bg-color);\n    filter: drop-shadow(0px 0px 7px var(--button-color));\n}\n\n#lockTableButton:hover img{\n    filter: var(--bg-color-svg-filter);\n}\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\n.calendarScreen{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n}\n\n#calendarPdf{\n    border: none;\n    box-shadow: 0px 0px 20px 5px var(--shadow-color);\n}\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n\n\n.libraryScreen {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n}\n\n.library {\n    display: flex;\n    justify-items: center;\n    border-right: 1px solid var(--secondary-color);\n    height: 100%;\n    padding: 0 90px 0 60px;\n}\n\n.libraryContainer {\n    grid-area: libraryContainer;\n    display: flex;\n    flex-direction: column;\n    margin: 0 20px;\n    align-items: center;\n    justify-content: center;\n    height: 500px;\n    width: 300px;\n    background-color: var(--primary-color);\n    border-radius: 30px;\n    border: 2px solid var(--secondary-color);\n}\n\n.libraryContainer p {\n    margin: 20px 0;\n}\n\n.mainText {\n    font-size: 20px;\n    color: var(--text-color);\n}\n\n.secondaryText {\n    font-size: 15px;\n    color: var(--text-color);\n}\n\n.librarySearch {\n    background-color: var(--bg-color);\n    border: var(--primary-color);\n    height: 30px;\n    border-radius: 30px;\n}\n\n.librarySearch input {\n    width: 100%;\n    height: 100%;\n    border: none;\n    background-color: transparent;\n    color: var(--text-color-2);\n    font-size: 20px;\n    padding: 0px 10px;\n}\n\n.semesterSelection {\n    display: grid;\n    grid-template-rows: repeat(4, 1fr);\n    grid-template-columns: repeat(2, 1fr);\n    gap: 8px;\n}\n\n.semesterSelection button {\n    background-color: transparent;\n    padding: 0px 10px;\n    border: 1px solid var(--button-color);\n    border-radius: 15px;\n    color: var(--button-color);\n    font-size: 18px;\n    cursor: pointer;\n    transition: 300ms;\n}\n\n.semesterSelection button:hover {\n    background-color: var(--button-color);\n    color: var(--bg-color);\n    filter: drop-shadow(0px 0px 3px var(--button-color));\n}\n\n.libraryContent {\n    height: 100%;\n    width: 70%;\n}\n\n\n\n\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n/* #================================================================================================================= */\n\n\n.gradeScreen{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n    overflow-y: auto;\n    padding-bottom: 50px;\n}\n\n.gradeContainer{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    min-width: 250px;\n    min-height: 30px;\n    border-radius: 20px;\n    border: 2px solid var(--secondary-color);\n    box-shadow: 0px 0px 20px 5px var(--shadow-color);\n    background-color: var(--primary-color);\n    padding: 30px 15px;\n    margin: 10px;\n}\n\n.subjectArea {\n    display: flex;\n    flex-direction: column;\n    justify-content: start;\n    align-items: center;\n    width: 1000px;\n    min-height: 325px;\n}\n\n.subject {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 325px;\n}\n\n.subjectName{\n    color: var(--text-color);\n    font-size: 24px;\n    font-weight: bold;\n    width: 90%;\n    height: 25px;\n    padding: 0 0 5px 5px;\n    border-bottom: 2px solid var(--secondary-color);\n}\n\n.subjectSubarea {\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    height: 90%;\n    width: 90%;\n    padding: 10px 20px;\n}\n\n.gradeArea {\n    width: 50%;\n    height: 100%;\n    justify-content: center;\n    align-items: center;\n    color: var(--text-color);\n    font-size: 20px;\n    border-right: 2px solid var(--secondary-color);\n    padding: 5px;\n}\n\n.grade {\n    display: flex;\n    justify-content: start;\n    align-items: center;\n    width: 100%;\n    height: 60px;\n    padding-left: 10%;\n}\n\n.grade p{\n    color: var(--text-color);\n    font-size: 20px;\n    width: 100px;\n    padding: 0 20px;\n}\n\n.grade progress {\n    height: 40px;\n}\n\n.addGradeArea {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 60px;\n}\n\n.attendanceArea {\n    display: flex;\n    flex-direction: column;\n    width: 50%;\n    height: 100%;\n    justify-content: center;\n    align-items: center;\n    color: var(--text-color);\n    font-size: 20px;\n    padding: 5px;\n}\n\n@keyframes growProgressBar {\n    0%,\n    33% {\n        --pgPercentage: 0;\n    }\n    100% {\n        --pgPercentage: var(--value);\n    }\n}\n\n@property --pgPercentage {\n    syntax: \"<number>\";\n    inherits: false;\n    initial-value: 0;\n}\n\n.attendanceBar {\n    --bar-size: 150px;\n    --pgPercentage: var(--value);\n    animation: growProgressBar 1s 1 forwards;\n    width: var(--bar-size);\n    height: var(--bar-size);\n    border-radius: 50%;\n    display: grid;\n    place-items: center;\n    background: radial-gradient(\n            closest-side,\n            var(--secondary-color) 80%,\n            transparent 0 99.9%,\n            var(--secondary-color) 0\n        ),\n        conic-gradient(var(--button-color) calc(var(--pgPercentage) * 1%), var(--bg-color) 0);\n    font-family: Helvetica, Arial, sans-serif;\n    font-size: calc(var(--bar-size) / 5);\n    color: var(--button-color);\n}\n\n.attendanceBar::before {\n    counter-reset: percentage var(--value);\n    content: counter(percentage